/**
 *
 * 日期工具类
 */
export default class DateUtil {
  /**
   * 转化日期格式
   * @param input Date
   * @returns {string} yyyy-MM-dd
   */
  static date2YyyyMMdd (input) {
    if (input == null) {
      throw new Error('input不能为空')
    }
    const date = new Date(input)
    let year = date.getFullYear()
    let month = (date.getMonth() + 1).toString().padStart(2, '0')
    let day = date.getDate().toString().padStart(2, '0')
    return `${year}-${month}-${day}`
  }
  /**
   * 转化日期格式
   * @param input Date
   * @returns {string} yyyy-MM-dd hh:mm:ss
   */
  static date2YyyyMMddhhmmss (input) {
    if (input == null) {
      throw new Error('input不能为空')
    }
    const date = new Date(input)
    let year = date.getFullYear()
    let month = (date.getMonth() + 1).toString().padStart(2, '0')
    let day = date.getDate().toString().padStart(2, '0')
    let hour = date.getHours().toString().padStart(2, '0')
    let minute = date.getMinutes().toString().padStart(2, '0')
    let seconds = date.getSeconds().toString().padStart(2, '0')
    return `${year}-${month}-${day} ${hour}:${minute}:${seconds}`
  }
  /**
   * 根据时区获取对应的日期时间
   * @param timezone
   * @param datestr
   * @returns {string}
   */
  static getDateTimeByTimeZone (timezone, dateStr) {
    let date = new Date(dateStr)
    let clientTime = new Date() // 客户端当前时间
    let offset = Math.floor(clientTime.getTimezoneOffset() * 60000) // 客户端时间与UTC时间的偏移量(毫秒)
    date.setTime(date.getTime() - offset) // 根据偏移量计算传入时间在客户端所在时区的对应时间
    return this.date2YyyyMMddhhmmss(date)
  }
}
