<template>
    <el-dialog
      :title="dialog.title"
      :visible.sync="dialog.visible"
      :before-close="handleClose"
      :close-on-click-modal = "false"
      width="500px"
    >
      <el-form label-width="80px" label-position="left" :model="formData" :rules="rules" ref="formData">
        <el-form-item label="日期" prop="date">
          <el-date-picker style="width: 100%;" type="date" placeholder="选择日期" v-model="formData.date"></el-date-picker>
        </el-form-item>
        <el-form-item label="姓名" prop="name" >
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address" >
          <el-input v-model="formData.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleCancel" size="small ">取 消</el-button>
        <el-button type="primary" @click="handleConfirm" size="small ">确 定</el-button>
      </span>
    </el-dialog>
</template>

<script>
import DateUtil from '../../../utils/DateUtil.js'
import { mapGetters } from 'vuex'

/**
* 接收的参数：title、data
* 返回的事件：cancel、confirm
*/
const cancelEvent = 'cancel'
const confirmEvent = 'confirm'

export default {
  name: 'addUppUser',
  // props: ['title', 'data'], // 接收父组件传递的参数
  props: { // 接收父组件传递的参数
    // 显示的标题
    title: {
      type: String,
      require: true,
      default: '标题'
    },
    // 接收的数据，当为添加时可不传，当为修改时必传
    data: {
      type: Object,
      require: false
    }
  },
  created () {
    // 开启对话框
    this.openDialog(this.title)
    // 有数据时，在表格显示数据
    if (this.data) {
      this.formData = this.data
    }
  },
  computed: {
    ...mapGetters('ui', [
      'getCurrentScreenWidth' // 映射到this.$store.getters['ui/getCurrentScreenWidth']
    ])
  },
  data () {
    return {
      formData: {},
      dialog: {
        visible: false,
        title: ''
      },
      rules: { // 表单校验规则
        date: [
          { required: true, message: '请输入日期', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在 3 到 15个字符', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入地址', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // ----------------事件处理
    handleClose () {
      // console.log('关闭')
      this.closeDialog(() => {
        // 发送事件给父组件
        this.$emit(cancelEvent)
      })
    },
    handleCancel () {
      // console.log('取消')
      this.closeDialog(() => {
        // 发送事件给父组件
        this.$emit(cancelEvent)
      })
    },
    handleConfirm () {
      // 参数校验
      console.log(this.$refs.formData)
      this.$refs.formData.validate((valid) => {
        if (valid) {
          // 校验通过
          this.closeDialog(() => {
            // 发送事件给父组件:this.$emit(事件名, 数据)
            this.formData = { ...this.formData, date: DateUtil.date2YyyyMMdd(this.formData.date) }
            this.$emit(confirmEvent, this.formData)
          })
        } else {
          // 校验不通过
          console.log('param error')
        }
      })
    },
    // ----------------封装方法
    // 开启对话框
    openDialog (title) {
      this.dialog = {
        visible: true,
        title
      }
    },
    // 关闭对话框
    closeDialog (callback) {
      // 关闭dialog
      this.dialog = {
        visible: false,
        title: ''
      }
      callback()
    }
  }
}
</script>

<style lang="scss" scoped>
.el-dialog{
  width: 100% !important;
}
</style>
