<template>
  <div class="user-container">
<!--    <el-scrollbar>-->
    <div class="top">
      <div class="search">
        <el-input
          style="width: 200px;"
          v-model="keyword"
          size="medium"
          prefix-icon="el-icon-search"
          placeholder="输入关键字搜索"
          @change="search"/>
      </div>
      <div class="btn-group">
        <el-button type="primary" size="medium" @click="handleAdd">
          新增
        </el-button>
        <el-button type="primary" size="medium">
          导入
        </el-button>
        <el-button type="primary" size="medium" @click="handlerExport" :disabled="multipleSelection.length ===0">
          导出
        </el-button>
      </div>
    </div>
<!--    </el-scrollbar>-->
    <AddUppUser v-if = "dialog.addOpen"  :title="dialog.title" @confirm = "compAdd" @cancel = "compCancel"></AddUppUser>
    <AddUppUser v-if = "dialog.uppOpen"  :title="dialog.title" :data="dialog.formData" @confirm = "compUpp" @cancel = "compCancel"></AddUppUser>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      :default-sort = "{prop: 'date', order: 'descending'}"
      @selection-change="handleSelectionChange"
      width="100%">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        type="index"
        :index="indexMethod()"
        label="序号"
        width="55">
      </el-table-column>
      <el-table-column
        sortable
        prop="date"
        label="日期"
        width="120">
        <template slot-scope="scope">{{ scope.row.date | date2YyyyMMdd }}</template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        label="操作"
        width="120">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="text"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
<!--    <el-scrollbar>-->
    <div class="page">
        <el-pagination
          layout="sizes, prev, pager, next, jumper"
          :total="tableData.length"
          @size-change = "handlerSizeChange"
          @current-change = "handlerCurrentChange"
          @prev-click = "handlerPreClick"
          @next-click = "handlerNextClick"
        >
        </el-pagination>
    </div>
<!--    </el-scrollbar>-->
  </div>
</template>

<script>
import AddUppUser from './AddUppUser'
import VueUtil from '../../../utils/VueUtil.js'

export default {
  name: 'User',
  components: {
    AddUppUser
  },
  created () {
    this.initTableData()
  },
  data () {
    return {
      // 对话框
      dialog: {
        title: '',
        addOpen: false, // 是否开启
        uppOpen: false, // 是否开启
        formData: {} // 表单数据
      },
      tableData: [], // 表格数据
      multipleSelection: [], // 选中记录
      keyword: '' // 搜索的字段
    }
  },
  methods: {
    handleSelectionChange (val) { // 处理选中选项
      console.log('选中的对象：', val)
      this.multipleSelection = val
    },
    indexMethod () { // 生成索引规则
      return 1
    },
    handleAdd () { // 处理添加事件
      // 开启添加对话框
      this.dialog = {
        title: '添加用户信息',
        addOpen: true
      }
    },
    handleEdit (index, row) { // 处理修改事件
      // console.log(index, row)
      console.log('编辑的id：', row.id)
      // 开启添加对话框
      this.dialog = {
        title: '修改用户信息',
        uppOpen: true,
        formData: row
      }
    },
    handleDelete (index, row) { // 处理删除事件
      // console.log(index, row)
      VueUtil.deleteConfirmAlert(this, () => {
        this.delete(row.id)
      })
    },
    handlerSizeChange (val) {
      console.log('改变条数', val)
    },
    handlerCurrentChange (val) {
      console.log('改变当前页', val)
    },
    handlerPreClick (val) {
      console.log('上一页', val)
    },
    handlerNextClick (val) {
      console.log('下一页', val)
    },
    handlerExport () { // 导出Excel
      import('../../../vendor/Export2Excel').then(excel => {
        const tHeader = ['id', '日期', '姓名', '地址']
        const test = this.multipleSelection
        console.log('data---- :', test)
        const filterVal = [
          'id',
          'date',
          'name',
          'address'
        ]
        const data = this.formatJson(filterVal, this.multipleSelection)
        console.log('data==== :', data)
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: 'user-list' // 导出文件的名，自定义就好
        })
      })
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v =>
        filterVal.map(j => {
          if (j === 'id') { // 此处如没有要格式化的列，可以不用此判断
            // return parseTime(v[j])
            return v[j]
          } else {
            return v[j]
          }
        })
      )
    },
    /* ********************组件事件********************** */
    compCancel () {
      this.dialog = {
        addOpen: false,
        uppOpen: false
      }
      console.log('取消')
    },
    compAdd (val) {
      this.dialog = {
        addOpen: false,
        uppOpen: false
      }
      console.log('确认', val)
      this.addUser(val)
    },
    compUpp (val) {
      this.dialog = {
        addOpen: false,
        uppOpen: false
      }
      console.log('确认', val)
      this.uppUser(val)
    },
    /* ********************接口请求********************** */
    initTableData () { // 初始化表格数据
      for (let i = 0; i < 10; i++) {
        let data = {
          id: i,
          // date: i < 9 ? '2020-01-0' + (i + 1) : '2020-01-' + (i + 1),
          date: new Date(),
          name: '陈木旋',
          address: '广东省广州市天河区长兴街道' + (i + 1) + '号'
        }
        this.tableData.push(data)
      }
    },
    search () { // 根据关键字搜索
      console.log('搜索的关键字', this.keyword)
      // this.tableData = this.tableData.filter(item => {
      //   return item.name.includes(this.keyword) || item.address.includes(this.keyword)
      // })
    },
    addUser (val) { // 添加
      val = { ...val, id: new Date().getTime() }
      this.tableData.push(val)
    },
    uppUser (val) { // 修改
      const index = this.tableData.findIndex(item => item.id === val.id)
      this.tableData.splice(index, 1, val)
    },
    delete (id) { // 删除
      console.log('删除的id：', id)
      const index = this.tableData.findIndex(item => item.id === id)
      this.tableData.splice(index, 1)
    }
  }
}
</script>
<style lang="scss" scoped>
.user-container{
  width: 100%;
  .top{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    .search{
      display: flex;
      justify-content: flex-start;
    }
    .btn-group{
      display: flex;
      justify-content: flex-end;
      /* 如果是移动端，保证按钮和搜索框之间有一定距离 */
      margin-left: 10px;
    }
  }
  .page{
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    overflow: hidden;
  }
}
</style>
