export default class VueUtil {
  /**
   * 确认弹出框框提示
   * @param vue vue实例
   * @param title 弹出框标题
   * @param callback 确认回调函数
   */
  static confirmAlert (vue, title, callback) {
    vue.$confirm(title, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      callback()
    }).catch(() => {
      console.log('取消')
    })
  }
  /**
   * 删除弹出框提示
   * @param vue vue实例
   * @param callback 确认回调函数
   */
  static deleteConfirmAlert (vue, callback) {
    VueUtil.confirmAlert(vue, '此操作将永久删除该记录, 是否继续?', callback)
  }
}
